import {
  SET_LOGIN_POPUP,
} from '../actions/loginActions';

const initialState = {
  loginControl: null,
};

const login = (state = initialState, action) => {
  switch (action.type) {
    case SET_LOGIN_POPUP:
      return {
        ...state,
        loginControl: action.data,
      };
    default:
      return state;
  }
};

export default login;
