import {
  SET_INTERCOM_VISIBILITY,
} from 'actions/intercomActions';

const initialState = {
  isVisible: true,
};

const intercom = (state = initialState, action) => {
  switch (action.type) {
    case SET_INTERCOM_VISIBILITY:
      return {
        ...state,
        isVisible: action.data,
      };
    default:
      return state;
  }
};

export default intercom;
