import _ from 'lodash';
import moment from 'moment';
import Logos from 'assets/images/InsurerLogos';
import { FORMSTATES } from '../actions/insuranceActions';
import { dateToStoreString, ISOStringToFormDate } from '../lib/dateFunctions';
import { validateFormValue } from '../lib/validatorsV2';

// stripe the attribue object with empty descriptions
export const compactCheckoutAttributes = attribute => (
  _.reduce(attribute, (result, arr) => {
    if (arr.description && !_.isEmpty(arr.description)) {
      result.push(arr);
    }
    return result;
  }, [])
);

export const getCheckoutParamsWording = (isAdHoc, adHocData, selectedPlanData, getParamsCallback) => {
  const params = {};
  let wording = null;
  try {
    if (isAdHoc) {
      params.sessionParams = adHocData.data;
      params.amountPayable = adHocData.amount_payable;
      params.price = adHocData.data.price;
    } else {
      params.sessionParams = getParamsCallback();
      params.amountPayable = selectedPlanData.policyInfo[selectedPlanData.planType].net_price;
      params.price = selectedPlanData.policyInfo[selectedPlanData.planType].price;
      wording = selectedPlanData.info.wording_url;
    }
  } catch (error) {
    console.log('Get params failed', error);
  }

  return {
    params,
    wording,
  };
};

export const getCheckoutHeaderDataCoverage = (categoryType, isAdHoc, adHocData, selectedPlanData, params) => {
  const insurerSlug = isAdHoc ? adHocData.insurer : selectedPlanData.info.insurer_slug;
  const insurerName = isAdHoc ? adHocData.data.client_display_name : selectedPlanData.info.insurer_name;
  const productName = isAdHoc ? adHocData.policy_name : selectedPlanData.info.product_name;
  const date = isAdHoc ? ISOStringToFormDate(adHocData.created_at) : dateToStoreString(new Date());
  const coverage = _.startCase(isAdHoc ? adHocData.data.session_params.policy_type : params.sessionParams.session_params.policy_type);

  return {
    headerData: {
      logo: Logos[insurerSlug],
      logoName: `${insurerSlug}.svg`,
      insurerName,
      insurerDesc: productName,
      data1: 'Type',
      data1Value: categoryType,
      data2: 'Date',
      data2Value: date,
    },
    coverage,
  };
};

// Strip empty details
export const getCompactSubDetails = (subdetails) => {
  const newSubDetail = subdetails;
  if (subdetails.leftDetail && subdetails.leftDetail.attributes && _.isEmpty(subdetails.leftDetail.attributes)) {
    newSubDetail.leftDetail = newSubDetail.rightDetail;
    delete newSubDetail.rightDetail;
  }

  if (subdetails.rightDetail && subdetails.rightDetail.attributes && _.isEmpty(subdetails.rightDetail.attributes)) {
    delete newSubDetail.rightDetail;
  }

  return newSubDetail;
};

