// used for slider control, simplify costs
export const simplifyCost = (value) => {
  try {
    const symbols = ['', 'k', 'm'];
    let valueToUse = parseInt(value, 10);
    const initialMoreThan1000 = valueToUse > 1000;
    let symbolIndex = 0;
    while (initialMoreThan1000 && valueToUse > 999) {
      valueToUse *= 0.001;
      symbolIndex += 1;
    }

    return `S$${valueToUse}${symbols[symbolIndex]}`;
  } catch (error) {
    console.log('Parse cost failed');
  }

  return null;
};

// Simpifly costs but takes in array
export const simplifyCostArray = array => (
  _.reduce(array, (result, value) => {
    const newValue = simplifyCost(value);
    if (newValue) {
      result.push(newValue);
    }
    return result;
  }, [])
);

// used to remap data from formfield API to be compatible with
// detail view
export const remapFormFields = (array) => {
  if (!array) {
    return {};
  }

  return _.reduce(array, (result, field) => {
    const newResult = {
      ...result,
      [field.name]: {
        ...field,
      },
    };

    return newResult;
  }, {});
};

// Hide nav and footer if mobile
export const hideNavFooterIfMobile = (location, doSetIntercomVisibility) => {
  if (location && location.pathname && _.includes(location.pathname, 'mobile')) {
    window.$('.navigation').css('cssText', 'display: none !important;');
    window.$('.footer').css('cssText', 'display: none !important;');

    doSetIntercomVisibility(false);
  }
};

export const routeHandler = (form, formState, formIndex, match, history, formStates = null) => {
  let index = formState;
  const formStatesToUse = formStates || FORMSTATES;

  try {
    if (match.params.page) {
      const newPath = _.find(formStatesToUse, state => _.includes(state.path, match.params.page));
      if (formState === formStatesToUse.SUBMITTED.index && newPath.index !== formStatesToUse.SUBMITTED.index) {
        let extraURL = '';
        if (history.params) {
          if (history.params.insurer) {
            extraURL = `${extraURL}/${history.params.insurer}`;
          }

          if (history.params.product) {
            extraURL = `${extraURL}/${history.params.product}`;
          }

          if (history.params.checkout) {
            extraURL = `${extraURL}/${history.params.checkout}`;
          }
        }
        history.push(`/${form}${formStatesToUse.SUBMITTED.path}${extraURL}`);
        // eslint-disable-next-line
        index = formStatesToUse.SUBMITTED.index;
      } else if (index < newPath.index) {
        history.goBack();
      } else {
        // eslint-disable-next-line
        index = newPath.index;
      }
    } else {
      index = 0;
    }
  } catch (error) {
    // eslint-disable-next-line no-empty
  }

  return ({
    formIndex: formIndex !== index ? index : null,
  });
};

export const getFieldsFromForms = (pages) => {
  let allFields = [];
  try {
    allFields = _.reduce(pages, (result, page) => {
      _.each(page.forms, (f) => {
        result.push(f);
      });
      return result;
    }, []);
  } catch (error) {
    // eslint-disable-next-line
  }

  return allFields;
};

export const generateRequestData = (allFields, values) => {
  let requestData = null;
  try {
    requestData = {};
    _.each(allFields, (field) => {
      const value = values[field.slug];

      if (value) {
        if (validateFormValue(field.slug, value, values, allFields)) {
          throw new Error('Request data is invalid');
        }

        const parsedValue = field.type === 'date' || field.type === 'end_date' ? value.value.toISOString() : value.value;
        if (parsedValue) {
          requestData[field.slug] = parsedValue;
        }
      }
    });
  } catch (error) {
    // eslint-disable-next-line
  }

  return requestData;
};


export const numberWithCommas = (x) => {
  try {
    let value = x.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    const splitted = value.toString().split('.');
    if (splitted.length === 1) {
      value = `${value}.00`;
    } else if (splitted.length === 2 && splitted[1].length === 1) {
      value = `${value}0`;
    }
    return value;
  } catch (error) {
    return x;
  }
};

export const getAmpTags = (selectedPlanData, planType) => {
  try {
    let discount = null;
    _.map(selectedPlanData.info.discount, (data) => {
      discount = data.display;
    });
    if (_.isEmpty(discount)) {
      discount = '0%';
    }

    return ({
      type: planType,
      partner: selectedPlanData.info.insurer_slug,
      price: selectedPlanData.policy_info[planType].premium.net_price,
      discount,
    });
  } catch (error) {
    console.log(`Travel choose plan Buy now Amp tag failed: ${error.message}`);
  }

  return {};
};


export const getFormValue = (valueItem) => {
  try {
    if (valueItem) {
      if (valueItem.type === 'select') {
        return valueItem.value.value;
      } else if (valueItem.type === 'multiple_select') {
        return _.map(valueItem.value, v => v.value);
      }

      return valueItem.value;
    }
  } catch (error) {
    // eslint-disable-next-line
  }

  return null;
};

export const revertFormValue = (field, key, value) => {
  try {
    if (field && key) {
      let v = value;
      if (field.type === 'select' && value && value !== '') {
        v = { display_name: value, value: value };
      } else if (field.type === 'multiple_select' && !_.isEmpty(value)) {
        v = _.map(value, v => ({ display_name: v, value: v }));
      } else if (field.type === 'number' && value) {
        // v = parseInt(value.toString(), 10);
      } else if (field.type === 'date' && value) {
        v = moment(value, 'DD/MM/YYYY').toDate();
        if (!v || v.toString() === 'Invalid Date') {
          v = moment(value).toDate();
        }
      }

      return { type: field.type, value: v };
    }
  } catch (error) {
    // eslint-disable-next-line
  }

  return null;
};

export const revertFormValuesUsingDefault = (defaultValues, values, oldValues = {}) => {
  try {
    return _.reduce(defaultValues, (result, field, key) => {
      const newResult = { ...result };
      const value = revertFormValue(field, key, values[key]);
      newResult[key] = value && value.value && value.value !== '' ? value : oldValues[key];

      return newResult;
    }, {});
  } catch (error) {
    // eslint-disable-next-line
  }

  return null;
};

export const revertFormValues = (fields, values) => {
  try {
    return _.reduce(fields, (result, field) => {
      const newResult = { ...result };
      const value = revertFormValue(field, field.slug, values[field.slug]);
      newResult[field.slug] = value && value.value && value.value !== '' ? value : values[field.slug];

      return newResult;
    }, {});
  } catch (error) {
    // eslint-disable-next-line
  }

  return null;
};


export const extractSearchParams = (searchString) => {
  try {
    const search = searchString.slice(1);
    const splitParts = search.split('&');
    const params = _.reduce(splitParts, (result, part) => {
      const newResult = { ...result };
      const split = part.split('=');

      if (split.length > 1 && split[0] !== '') {
        // eslint-disable-next-line
        newResult[split[0]] = split[1];
      }

      return newResult;
    }, {});

    return _.isEmpty(params) ? null : params;
  } catch (error) {
    // eslint-disable-next-line
  }

  return null;
};

export const parseConstants = (constants) => {
  const results = {
    gender: [
      'Male',
      'Female',
    ],
    maritalStatus: [
      'Single',
      'Married',
    ],
    race: [
      'Chinese',
      'Malay',
      'Indian',
      'Eurasian',
      'Others',
    ],
    smoker: [
      'Smoker',
      'Non Smoker',
    ],
  };

  try {
    results.countries = _.reduce(constants.countries, (result, data) => {
      result.push(data.name);
      return result;
    }, []);

    results.nationalities = _.reduce(constants.nationalities, (result, data) => {
      result.push(data.name);
      return result;
    }, []);

    results.occupations = _.reduce(constants.occupations, (result, data) => {
      result.push(data.name);
      return result;
    }, []);

    results.relationships = _.reduce(constants.relationships, (result, data) => {
      if (data.name !== 'Myself' && !_.includes(data.name, 'Child')) {
        result.push(data.name);
      }
      return result;
    }, []);
    results.relationships.push('My Child');

    results.idTypes = _.reduce(constants.id_types, (result, data) => {
      result.push(data.name);
      return result;
    }, []);

    results.industries = _.reduce(constants.industries, (result, data) => {
      result.push(data.name);
      return result;
    }, []);

    return results;
  } catch (error) {
    return null;
  }
};
