import _ from 'lodash';
import { tldExists } from 'tldjs';
import { getFormValue } from 'lib/helperFunctions';

const nric = [];
nric.multiples = [2, 7, 6, 5, 4, 3, 2];

nric.isNricValid = (theNric) => {
  if (!theNric || theNric === '') {
    return false;
  }

  if (theNric.length !== 9) {
    return false;
  }

  let total = 0;
  let count = 0;
  let numericNric;
  const first = theNric[0];
  const last = theNric[theNric.length - 1];

  if (first !== 'S' && first !== 'T') {
    return false;
  }

  numericNric = theNric.substr(1, theNric.length - 2);

  if (_.isNaN(numericNric)) {
    return false;
  }

  while (numericNric !== 0) {
    total += (numericNric % 10) * nric.multiples[nric.multiples.length - (1 + count)];
    count += 1;
    numericNric /= 10;
    numericNric = Math.floor(numericNric);
  }

  let outputs;
  if (first === 'S') {
    outputs = ['J', 'Z', 'I', 'H', 'G', 'F', 'E', 'D', 'C', 'B', 'A'];
  } else {
    outputs = ['G', 'F', 'E', 'D', 'C', 'B', 'A', 'J', 'Z', 'I', 'H'];
  }

  return last === outputs[total % 11];
};

nric.isFinValid = (fin) => {
  if (!fin || fin === '') {
    return false;
  }

  if (fin.length !== 9) {
    return false;
  }

  let total = 0;
  let count = 0;
  let numericNric;
  const first = fin[0];
  const last = fin[fin.length - 1];

  if (first !== 'F' && first !== 'G') {
    return false;
  }

  numericNric = fin.substr(1, fin.length - 2);

  if (_.isNaN(numericNric)) {
    return false;
  }

  while (numericNric !== 0) {
    total += (numericNric % 10) * nric.multiples[nric.multiples.length - (1 + count)];
    count += 1;

    numericNric /= 10;
    numericNric = Math.floor(numericNric);
  }

  let outputs;
  if (first === 'F') {
    outputs = ['X', 'W', 'U', 'T', 'R', 'Q', 'P', 'N', 'M', 'L', 'K'];
  } else {
    outputs = ['R', 'Q', 'P', 'N', 'M', 'L', 'K', 'X', 'W', 'U', 'T'];
  }

  return last === outputs[total % 11];
};

// Validate a form value
export const validateFormValue = (key, data, values, allFields, specialFields) => {
  const field = _.find(allFields, f => f.slug === key);
  const isRequired = !field || (field && field.is_required === undefined) || (field && field.is_required);
  const isPersons = key === 'num_adult' || key === 'num_children';

  if (
    !isPersons &&
    isRequired && (
      (!data || (data && !data.value)) ||
      (data.value.constructor === String && data.value.trim() === '') ||
      (data.value.constructor === Array && _.isEmpty(data.value))
    )
  ) {
    return 'Field cannot be empty!';
  }

  if (data) {
    switch (data.type) {
      case 'text': {
        if (_.includes(key, 'name')) {
          // [a-zA-Z0-9]
          const reName = /^[^#&<>"~;$^%{}?/|\\=:*/[\]@]{2,30}$/;
          const reFullName = /^[^#&<>"~;$^%{}?/|\\=:*/[\]@]{2,60}$/;

          const testToUse = key === 'name' ? reFullName : reName;
          if (!testToUse.test(data.value)) {
            return 'Invalid name';
          }
        }

        if (key === 'address') {
          // @#%&*()-_+',./
          const reAddress = /^[^<>"~;$^{}?|\\=:[\]]{1,30}$/;
          const reNum = /^\d+$/;

          if (data.value && data.value.length > 30) {
            return 'Max character limit of 30 exceeded';
          }

          if (specialFields && specialFields.insurer_slug === 'msig') {
            const splittedString = _.split(data.value, ' ');
            if (splittedString && splittedString[0] && reNum.test(splittedString[0])) {
              return 'First word in address cannot be a number';
            }
          }

          if (!reAddress.test(data.value)) {
            return 'Invalid characters in address';
          }
        }

        if (key === 'blk_no') {
          const reName = /^[^&<>"~;$^%{}?/|\\=:*/[\]@]{1,20}$/;
          if (!reName.test(data.value)) {
            return 'Invalid block number';
          }
        }

        if (key === 'unit_no') {
          const reNum = /^\d+$/;
          if (reNum.test(data.value)) {
            return 'Unit number cannot be fully numeric';
          }
        }

        if (key === 'nric') {
          const reNric = /^[s|g|f|t]\d{7}[a-z]$/i;
          const idType = getFormValue(values.id_type);
          const nationality = getFormValue(values.nationality);

          const isNRIC = idType && _.includes(idType, 'NRIC');
          const isNRICPr = idType && _.includes(idType, 'NRIC (Singapore PR)');
          const isFIN = idType && !isNRIC;

          let nricCheck = false;

          if (isNRIC && !isNRICPr && nationality) {
            nricCheck = nationality === 'Singaporean';
          } else {
            nricCheck = true;
          }

          if (
            !nricCheck ||
            (isFIN && nationality === 'Singaporean')
          ) {
            return 'Nationality & type of ID mismatch';
          }

          let result = false;
          const valueToUse = data.value.toUpperCase();
          if (isNRIC) {
            result = nric.isNricValid(valueToUse);
          } else if (isFIN) {
            result = nric.isFinValid(valueToUse);
          } else {
            result = nric.isNricValid(valueToUse) || nric.isFinValid(valueToUse);
          }

          if (!reNric.test(valueToUse) || !result) {
            const idTypeText = idType || 'ID Number';
            return `Invalid ${idTypeText}`;
          }
        }

        break;
      }
      case 'date': {
        try {
          const startDate = getFormValue(values.start_date) || (field && field.min_date) || data.value;
          const endDate = getFormValue(values.end_date) || (field && field.max_date) || data.value;
          // eslint-disable-next-line
          if (!(startDate.getTime() <= data.value.getTime() <= endDate.getTime())) {
            return 'Invalid date';
          }
        } catch (error) {
          // eslint-disable-next-line
          return 'Invalid date';
        }

        break;
      }
      case 'number': {
        if (key === 'contact' || key === 'mobile_number') {
          // const reMobile = /^[3689]\d{7}$/;
          const reMobile = /^[89]\d{7}$/;
          if (!reMobile.test(data.value)) {
            return 'Invalid mobile number';
          }
        }
        break;
      }
      case 'postal': {
        if (key === 'postal') {
          const reNum = /^\d+$/;
          // value.length < 3
          if (data.value.length !== 6 || !reNum.test(data.value)) {
            return 'Invalid postal';
          }
        }

        break;
      }
      case 'select': {
        if (key === 'marital_status' && data.value.value.toUpperCase() !== 'SINGLE' && data.value.value.toUpperCase() !== 'MARRIED') {
          return 'Invalid marital status';
        }

        if (key === 'gender' && data.value.value.toUpperCase() !== 'MALE' && data.value.value.toUpperCase() !== 'FEMALE') {
          return 'Invalid gender';
        }

        break;
      }
      case 'email': {
        const reEmail = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        if (!reEmail.test(data.value) || !tldExists(data.value) || data.value.length > 60) {
          return 'Invalid email';
        }
        break;
      }
      case 'image_selection': {
        try {
          const isValid = _.find(field.options, opt => opt.value === data.value);
          if (field.is_required && !isValid) {
            return 'Invalid selection';
          }
        } catch (error) {
          // eslint-disable-next-line
        }

        break;
      }
      case 'age_group': {
        try {
          let count = 0;
          _.each(data.value, (v) => { count += v.count; });

          if (count < 2) {
            return 'Employee count cannot be less than 2';
          }
        } catch (error) {
          // eslint-disable-next-line
        }

        break;
      }
      case 'checkbox': {
        try {
          if (data.isRequired && !data.value) {
            return 'Please agree to this field!';
          }
        } catch (error) {
          // eslint-disable-next-line
        }

        break;
      }
      case 'persons': {
        const nameAdult = 'num_adult';
        const nameChildren = 'num_children';

        if (key === nameAdult) {
          const numAdult = (values[nameAdult] && values[nameAdult].value) || 0;
          const tripType = getFormValue(values.trip_type);
          const isSingleTrip = tripType && tripType === 'Single Trip';

          const minAdult = 1;
          const maxAdult = isSingleTrip ? 20 : 2;

          if (!(minAdult <= numAdult && numAdult <= maxAdult)) {
            return `Number of adults must be within ${minAdult} and ${maxAdult}`;
          }
        }

        if (key === nameChildren) {
          const numChildren = (values[nameChildren] && values[nameChildren].value) || 0;
          const minChildren = 0;
          const maxChildren = 8;

          if (!(minChildren <= numChildren && numChildren <= maxChildren)) {
            return `Number of children must be within ${minChildren} and ${maxChildren}`;
          }
        }
        break;
      }
      default: {
        break;
      }
    }
  }

  return null;
};

export const isRenderable = (values, conditions) => {
  let result = true;
  try {
    _.each(conditions || [], (condition, index) => {
      const isValid = values[condition.key].value === condition.value;
      if (index === 0) {
        result = isValid;
      } else {
        result = condition.type === 'or' ? isValid || isRenderable : isValid && isRenderable;
      }
    });
  } catch (e) {
    result = false;
  }

  return result;
};

// Validate a form value
export const validateForm = (values, subset, formData, specialFields) => {
  const errors = {};

  try {
    const subsetToUse = subset || _.reduce(formData, (result, field) => (field.slugs ? [...result, ...field.slugs] : [...result, field.slug]), []) || [];

    if (formData) {
      _.each(formData, (field) => {
        if (isRenderable(values, field.conditions)) {
          const slugs = field.slugs ? field.slugs : [field.slug];

          _.each(slugs, (slug) => {
            const isMatch = _.find(subsetToUse, f => f === slug);
            if (isMatch) {
              const validateResult = validateFormValue(slug, (values || {})[slug], values || {}, formData, specialFields);

              if (validateResult) {
                errors[slug] = validateResult;
              }
            }
          });
        }
      });
    }
  } catch (error) {
    // eslint-disable-next-line
    console.log(error);
  }

  return (!_.isEmpty(errors) && errors) || {};
};
