import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { Container } from 'reactstrap';
import PropTypes from 'prop-types';
import withTracker from 'withTracker';
import { hotjar } from 'react-hotjar';
import Loadable from 'react-loadable';

import NavigationContainer from 'containers/NavContainer';
import LoadingScreen from 'components/Templates/LoadingScreen/LoadingScreen';
import Page from 'components/Page';
// import IntercomMain from 'components/Intercom/IntercomMain';
// import { landingTags } from 'metaTagsConstants';

// import DateInner from '../components/Forms/DatePicker/DateInner';

import { getConstants } from '../actions/insuranceActions';
import { checkLoginStatus } from '../actions/userActions';
// import { setDatepickerInner } from '../actions/datepickerActions';
import { isProduction } from '../lib/tagFunctions';
import Routes from './Routes';

const IntercomMain = Loadable({
  loader: () => import(/* webpackChunkName: "IntercomMain" */ 'components/Intercom/IntercomMain'),
  loading: () => null,
  modules: ['IntercomMain']
});

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isIntercomVisible: false,
    };

    this.onMovement = this.onMovement.bind(this);
  }

  componentDidMount() {
    const { checkLogin } = this.props;

    // window.fbq('init', process.env.REACT_APP_FB_TAG);
    // window.fbq('set', 'agent', 'tmgoogletagmanager', process.env.REACT_APP_FB_TAG);
    // window.fbq('track', 'PageView');

    // doSetDatepickerInner(this.datepickerInner);

    const hotjarSettings = isProduction ? { hjid: 1218971, hjsv: 6 } : { hjid: 1193604, hjsv: 6 };
    hotjar.initialize(hotjarSettings.hjid, hotjarSettings.hjsv);

    checkLogin();
    // getConstantsData();

    this.timeout = setTimeout(() => this.setState({ isIntercomVisible: true }), 10000);
    window.addEventListener('scroll', this.onMovement);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.onMovement);
  }

  onMovement() {
    const { isIntercomVisible } = this.state;
    const { getConstantsData } = this.props;

    if (!isIntercomVisible) {
      this.setState({
        isIntercomVisible: true,
      });

      getConstantsData();

      window.removeEventListener('scroll', this.onMovement);
      clearTimeout(this.timeout);
    }
  }

  renderInner() {
    return (
      <Container fluid className="main-container-fluid">
        <Routes />
      </Container>
    );
  }

  render() {
    const { isIntercomVisible } = this.state;
    // const hotJar = (
    //   <script>
    //     {
    //       // eslint-disable-next-line
    //       (function(h,o,t,j,a,r){
    //         // eslint-disable-next-line
    //         h.hj=h.hj||function(){(h.hj.q=h.hj.q||[]).push(arguments)};
    //         // eslint-disable-next-line
    //         h._hjSettings=hotjarSettings;
    //         // eslint-disable-next-line
    //         a=o.getElementsByTagName('head')[0];
    //         // eslint-disable-next-line
    //         r=o.createElement('script');r.async=1;
    //         // eslint-disable-next-line
    //         r.src=t+h._hjSettings.hjid+j+h._hjSettings.hjsv;
    //         a.appendChild(r);
    //         // eslint-disable-next-line
    //       })(window,document,'https://static.hotjar.com/c/hotjar-','.js?sv=')
    //     }
    //   </script>
    // );

    return (
      <Page
        // id="landing"
        // rest={landingTags}
        className="App"
      >
        <NavigationContainer />
        {this.renderInner()}
        <LoadingScreen />
        {isIntercomVisible && <IntercomMain appID="yltolfa1" />}
      </Page>
    );
  }
}

App.defaultProps = {
};

App.propTypes = {
  checkLogin: PropTypes.func.isRequired,
  getConstantsData: PropTypes.func.isRequired,
  // setLoginPopup: PropTypes.func.isRequired,
  // doSetDatepickerInner: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  // isInitalLoad: state.userDetails.isInitalLoad,
  login: state.login,
});

const mapDispatchToProps = dispatch => ({
  checkLogin: () => {
    dispatch(checkLoginStatus());
  },
  getConstantsData: () => {
    dispatch(getConstants());
  },
  // setLoginPopup: (popup, loginContainer) => {
  //   dispatch(setLoginPopup(popup, loginContainer));
  // },
  // doSetDatepickerInner: (datepickerInner) => {
  //   dispatch(setDatepickerInner(datepickerInner));
  // },
});

export default withRouter(withTracker(connect(mapStateToProps, mapDispatchToProps)(App)));
