export const SET_REDIRECT_DATA = 'SET_REDIRECT_DATA';

export const setRedirectPopup = (productData, desc, func) => (dispatch) => {
  dispatch({
    type: SET_REDIRECT_DATA,
    data: {
      productData,
      desc,
      func,
    },
  });
};
