import _ from 'lodash';
import React, { Component } from 'react';
import { withRouter } from 'react-router';
import PropTypes from 'prop-types';
// import Helmet from 'react-helmet';
import logo from 'assets/images/common/PolicyPalLogo.png';

import { isProduction } from 'lib/tagFunctions';
import { tags } from '../../metaTagsConstants';

const SITE_URL = isProduction ? 'https://www.policypal.com' : 'https://web.policypal.com';

const FACEBOOK_APP_ID = process.env.REACT_APP_FB_ID;

const defaultTitle = 'PolicyPal 2.0';
const defaultDescription = 'PolicyPal is the insurance mobile app that helps you manage and get quality insurance policies at affordable prices.';
const defaultImage = `${SITE_URL}${logo}`;
const defaultTwitter = '@hipolicypal';
const defaultSep = ' - ';

class Page extends Component {
  componentDidMount() {
    // this.setTagsInterval();
    this.setTags();
  }

  componentDidUpdate(prevProps) {
    // const { rest } = this.props;
    const currentPage = this.getPath(prevProps.location);
    const nextPage = this.getPath(this.props.location);

    if (currentPage !== nextPage) {
      // this.setTagsInterval();
      this.setTags();
    }
    // const oldId = rest && rest.id;
    // const newId = nextProps.rest && nextProps.rest.id;

    // console.log(newId);
    // if (newId && oldId !== newId) {
    //   this.setTags();
    // }
  }

  setTagsInterval() {
    let count = 0;
    clearInterval(this.interval);
    this.interval = setInterval(() => {
      if (count < 10) {
        this.setTags();
        count += 1;
      } else {
        clearInterval(this.interval);
      }
    }, 10);
  }

  getTokens() {
    let tokens = ['landing'];
    try {
      tokens = this.props.location.pathname.split('/');
      tokens = tokens.slice(1, tokens.length);
      if (tokens.length > 0 && tokens[0] === '') {
        tokens[0] = 'landing';
      }
    } catch (error) {
      // eslint-disable-next-line
    }

    return tokens;
  }

  getPath(location) {
    const { pathname, search } = location;
    return `${pathname}${search}`;
  }

  getMetaTags(
    {
      title,
      description,
      image,
      contentType,
      twitter,
      noCrawl,
      published,
      updated,
      category,
      tags,
    },
    pathname,
  ) {
    const theTitle = title
      ? (defaultTitle + defaultSep + title).substring(0, 60)
      : defaultTitle;
    const theDescription = description
      ? description.substring(0, 155)
      : defaultDescription;
    const theImage = image ? `${SITE_URL}${image}` : defaultImage;

    const metaTags = [
      { itemprop: 'name', content: theTitle },
      { itemprop: 'description', content: theDescription },
      { itemprop: 'image', content: theImage },
      { name: 'keywords', content: tags },
      { name: 'title', content: theTitle },
      { name: 'description', content: theDescription },
      { name: 'twitter:card', content: 'summary_large_image' },
      { name: 'twitter:site', content: defaultTwitter },
      { name: 'twitter:title', content: theTitle },
      { name: 'twitter:description', content: theDescription },
      { name: 'twitter:creator', content: twitter || defaultTwitter },
      { name: 'twitter:image:src', content: theImage },
      { property: 'og:title', content: theTitle },
      { property: 'og:type', content: contentType || 'website' },
      { property: 'og:url', content: SITE_URL + pathname },
      { property: 'og:image', content: theImage },
      { property: 'og:description', content: theDescription },
      { property: 'og:site_name', content: defaultTitle },
      { property: 'fb:app_id', content: FACEBOOK_APP_ID },
    ];

    // if (noCrawl) {
    //   metaTags.push({ name: 'robots', content: 'noindex, nofollow' });
    // }

    // if (published) {
    //   metaTags.push({ name: 'article:published_time', content: published });
    // }
    // if (updated) {
    //   metaTags.push({ name: 'article:modified_time', content: updated });
    // }
    // if (category) {
    //   metaTags.push({ name: 'article:section', content: category });
    // }
    // if (tags) {
    //   metaTags.push({ name: 'article:tag', content: tags });
    // }

    return metaTags;
  }

  setTags() {
    const { location } = this.props;

    try {
      const tokens = this.getTokens();
      const rest = tags[tokens[0]];

      if (rest) {
        const path = this.getPath(location);
        const metaTags = this.getMetaTags(rest, path);

        document.title = rest.title ? defaultTitle + defaultSep + rest.title : defaultTitle;
        document.querySelector('link[rel="canonical"]').setAttribute('href', SITE_URL + path);

        _.each(metaTags, (tag) => {
          if (tag.itemprop) {
            document.querySelector(`meta[itemprop="${tag.itemprop}"]`).setAttribute('content', tag.content);
          } else if (tag.name) {
            document.querySelector(`meta[name="${tag.name}"]`).setAttribute('content', tag.content);
          } else if (tag.property) {
            document.querySelector(`meta[property="${tag.property}"]`).setAttribute('content', tag.content);
          }
        });
      }
    } catch (error) {
      // eslint-disable-next-line
    }
  }

  render() {
    const {
      children,
      id,
      className,
      // location,
      // ...rest
    } = this.props;

    this.setTags();
    // const path = this.getPath(location);

    return (
      <div id={id} className={className}>
        {/* <Helmet
          htmlAttributes={{
            lang: 'en',
            itemscope: undefined,
            itemtype: `http://schema.org/${rest.schema || 'WebPage'}`,
          }}
          title={
            rest.title ? defaultTitle + defaultSep + rest.title : defaultTitle
          }
          link={[{
              rel: 'canonical',
              href: SITE_URL + path,
          }]}
          meta={this.getMetaTags(rest, path)}
        /> */}
        {children}
      </div>
    );
  }
}

Page.defaultProps = {
  className: '',
  rest: null,
  id: '',
};

Page.propTypes = {
  id: PropTypes.string,
  className: PropTypes.string,
  location: PropTypes.object.isRequired,
  rest: PropTypes.object,
};

export default withRouter(Page);
